import DPIntroCore, { EVENTS, LAYER_CLASSES } from './DPIntroCore';
import DPBoundingBox from './DPBoundingBox';
import DPIncUtils from '../../../../includes/assets/js/modules/DPIncUtils';
import DpitHooks from '../../../../includes/assets/js/modules/DpitHooks';




export default class DPIntroBuilderCore extends DPIntroCore {
	constructor(mainWrapperElement, mainCfg, tourCfg, bulletsAnimation) {
		if (mainCfg.dpDebugEn) console.log('DPIntroBuilderCore');
		super(mainWrapperElement, mainCfg);
		this.tourCfg = tourCfg;
		this.mainCfg = mainCfg;
		this.isBuilder = true;
		this.isTriggerMode = this.mainCfg.builderMode == 2;
		this._optionsOverride = {
			exitOnOverlayClick: false,
			enable_interaction: '1',
			exitOnEsc: false,
			//controlsAutoFocusEnable: false,
			centerTopBottomPosOffset: 120,
			leave_overlay_always: true
		};

		this.options = { ...this.options, ...this._optionsOverride };

		this.fullscreenBulletsAnimation = bulletsAnimation;

		if (this.isTriggerMode) {
			this.start = () => {
				this.hideOverlay();
			};
		}

		DpitHooks.addAction(EVENTS.onInit, () => {
			if (this.buttonPrevLayer) {
				$(this.buttonPrevLayer).hover(
					() => this.doAction(EVENTS.onButtonHover, { id: 'back', hoverIn: true }),
					() => this.doAction(EVENTS.onButtonHover, { id: 'back', hoverIn: false })
				);
			}

			if (this.buttonNextLayer) {
				$(this.buttonNextLayer).hover(
					() => this.doAction(EVENTS.onButtonHover, { id: 'next', hoverIn: true }),
					() => this.doAction(EVENTS.onButtonHover, { id: 'next', hoverIn: false })
				);
			}
		});

		$('.' + LAYER_CLASSES.wrapper).addClass('dpit_builder');
	}

	_shouldBeStepSkipped(stepItem, isMobileSize) {
		if (this.mainCfg.builderMode === '1' && !this.mainCfg.mobileAltStepMode) {
			return super._shouldBeStepSkipped(stepItem, isMobileSize);
		}
		else return false;
	}

	static initBuilderContainer(mainElement) {
		let dpIntroBuilderContainer = document.createElement('div');
		dpIntroBuilderContainer.className = LAYER_CLASSES.builder;
		mainElement.appendChild(dpIntroBuilderContainer);
		return dpIntroBuilderContainer;
	}

	nextStepOnLastStep() {
		this._currentStep = undefined;
		return;
	}

	showIntroLayers(showOverlay = true, showWrapper = true) {
		if (!this.showIntroLayersDisabled) super.showIntroLayers(showOverlay, showWrapper);
	}

	stepObject4Hooks(stepCore) {
		return stepCore ?? null;
	}

	setOptions(options) {
		super.setOptions({ ...options, ...this._optionsOverride });
	}

	initSteps() {
		if (!this.introItems.length)// not added as a new in builder
		{
			return super.initSteps();
		}
		else return this.introItems;
	}


	onSkipButtonClick() {
		alert('Ending the tour is not allowed in builder mode. To close the builder, use the close button in the corner.');
	}

	_restoreTour = () => {
		this._disableInteraction();
		this._restoreCurrentStepElement();
		if (!this.isTriggerMode)
			this.showIntroLayers();
	}


	_restoreCurrentStepElement = () => {
		if (this.currentTargetItem) {
			this.setTargetElementClasses(this.currentTargetItem.element);
			this._clearContrastAdjustment();
			this.fixTargetLowContrast(this.currentTargetItem);
		}
	}


	getBottomScrollOffset(flyingObjectsBB, targetBB, tooltipBB, winHeight, fixedTooltipOffset, isFixedOnTop) {
		let builderEditPanel = $('.dp-pull-out-panel--active');
		if (builderEditPanel.length && !fixedTooltipOffset.top && !fixedTooltipOffset.bottom) {
			const builderEditPanelBB = DPBoundingBox.getElBoundingBox(builderEditPanel[0], false, true);
			if (builderEditPanelBB.bottom + 5 >= winHeight) {  // edit panel sticky on bottom

				if ($(this.arrowLayer).hasClass(LAYER_CLASSES.arrow + '--top')) return tooltipBB.bottom - (winHeight - builderEditPanelBB.height) + 20;
				else return tooltipBB.top - 20;
			}
		}
		return super.getBottomScrollOffset(flyingObjectsBB, targetBB, tooltipBB, winHeight, fixedTooltipOffset, isFixedOnTop);
	}

	_onKeyDown(e) {
		if (!$('.dp-step-edit__intro-text').is(':focus')) {
			super._onKeyDown(e);
		}
	}



	addNewStepAt = (stepIdx = null) => {
		if (stepIdx === null) stepIdx = this._currentStep;

		const currentUrl = DPIncUtils.removeURLParameters(window.location.href, this.mainCfg.queryParamsDefs);
		const currentUrlRel = DPIncUtils.absToRelativeUrl(currentUrl, this.mainCfg.siteUrl);

		const newStepBasic = {
			...DPIncUtils.initStepParams(this.mainCfg.stepDefinitions),
			isNewStep: true,
			step: stepIdx + 1,
			url: stepIdx > 0 ? '$keep-prev-step-url' : DPIncUtils.unifyUrl(currentUrlRel, false, false)
		};

		const newStep = {
			...newStepBasic,
			selector: newStepBasic.element,
			element: $(newStepBasic.element)[0] || null,
			scrollTo: this.options.scrollTo,
			url: stepIdx > 0 ? '$keep-prev-step-url' : currentUrl
		};

		if (stepIdx < this.introItems.length) {
			for (let i = stepIdx; i < this.introItems.length; i++) {
				this.introItems[i].step++;
			}
		}
		if (this.options.showBullets) {
			$(this.bulletsLayer).find('ul').append(this.createBullet(this.introItems.length + 1, true, false, this._bulletClickCallback));
		}
		this.introItems.splice(stepIdx, 0, newStep);
		return newStepBasic;
	}

	removeStepAt = (stepIdx = null) => {
		if (stepIdx === null) stepIdx = this._currentStep;
		const isAtEnd = stepIdx === this.introItems.length - 1;
		if (stepIdx < this.introItems.length) {
			for (let i = stepIdx; i < this.introItems.length; i++) {
				this.introItems[i].step--;
			}
		}
		this.introItems.splice(stepIdx, 1);
		const newStepIdx = isAtEnd ? this._currentStep - 1 : this._currentStep;
		if (this.options.showBullets) {
			$(this.bulletsLayer).find('ul li:last-child').remove();
		}
		return newStepIdx >= 0 ? this.introItems[newStepIdx] : null;
	}

}