import DPIntroBuilder from './modules/DPIntroBuilder';
import DPIncUtils from '../../../includes/assets/js/modules/DPIncUtils';
import AdminThemeColors from '../../../includes/assets/js/modules/AdminThemeColors';

jQuery(window).on('load', () => {
	window.$ = jQuery;
	DPIncUtils.loadBuilder(window.dpIntroTourPublicConfig, (mainCfg, tourCfg) => {
		new DPIntroBuilder(mainCfg, tourCfg);
		new AdminThemeColors(mainCfg);
	});
});
