import React, { Component } from 'react';
import Select from 'react-select';
import classNames from 'classnames';
import IsMobile from 'is-mobile';
import './DPSelect.scss';

export default class DPSelect extends Component {
	constructor(props) {
		super(props);
		this.isMobile = IsMobile();
	}

	render() {
		let select = null;
		// console.log(this.isMobile);
		if (this.props.forceNative || this.isMobile) {
			const nativeSelectClass = classNames({
				[this.props.className]: true,
				[`${this.props.classNamePrefix}__native`]: this.props.classNamePrefix,
				[`${this.props.className}__native`]: !this.props.classNamePrefix,
			});
			select = (
				<select
					className={nativeSelectClass}
					value={this.props.value ? this.props.value.value : this.props.defaultValueNative}
					onChange={(event) => {
						if (this.props.onChange) { this.props.onChange({ value: event.target.value, label: event.target[event.target.selectedIndex].text }); }
					}}
				>{

						this.props.options.map((option, i) => (
							<option
								value={option.value}
								defaultValue={this.props.defaultValueNative}
								key={i}
							>{option.label}
							</option>
						))
					}
				</select>
			);
		} else {
			select = <Select {...this.props} />;
		}
		return select;
	}
}
