import React from 'react';
import ReactDOM from 'react-dom';
import DPIntroBuilderCore from './DPIntroBuilderCore';
import DPIntroTour from './DPIntroTour';

import Index from '../apps/TourStepEditor/Index';
// import registerServiceWorker from '../apps/TourStepEditor/registerServiceWorker';

export default class DPIntroBuilder extends DPIntroTour {
	constructor(mainCfg, tourCfg) {
		if (mainCfg.dpDebugEn) console.log('DPIntroBuilder');
		super(mainCfg, tourCfg);
		this.createEditor();
	}

	isStepsEnough2Start() {
		return true;
	}

	createCoreInstance() {
		return new DPIntroBuilderCore(this.dpIntroMain, this.mainCfg, this.tourCfg, this.bulletsAnimation);
	}

	createEditor() {
		ReactDOM.render(
			<Index
				mainCfg={this.mainCfg}
				tourCfg={this.tourCfg}
				core={this.core}
				secondLayerIntroModule={this}
			/>,
			DPIntroBuilderCore.initBuilderContainer(this.dpIntroMain)
		);
		// registerServiceWorker();
	}
}
