import DPIncUtils from './DPIncUtils';

export default class DpTinyMceDialog {
	constructor(config, onTmceSaved, onTmceCancel, dpDebugEn) {
		this.config = config;
		this.onTmceCancel = onTmceCancel;
		if (dpDebugEn) console.log('DpTinyMceDialog');
		$('#dp-step-content-edit .dp-tmce-dialog__btn').on('click', () => {
			if (onTmceSaved) {
				$('#dp-tmce-editor-tmce').trigger('click');
				onTmceSaved(this.target, window.tinyMCE.activeEditor.getContent());
			}
			this.close(false);
		});

		$('#dp-step-content-edit .dp-tmce-dialog__close,#dp-step-content-edit__backdrop').on('click', () => {
			this.close();
		});
		$(window).on('keydown', this.keydown.bind(this));



		//wp.editor.initialize('dp-tmce-editor', { tinymce: true });
	}

	keydown(event) {

		// Escape key.
		if (this.modalOpened && event.keyCode === 27) {
			this.close();
		}
	}

	open(target, data, stepIdx) {
		this.modalOpened = true;
		if (stepIdx !== false && !isNaN(stepIdx)) {
			const step = DPIncUtils.getNthStr(stepIdx);
			$('#dp-step-content-edit .dp-tmce-dialog__title').text(this.config.title.replace('%s', step));
		}
		$('#dp-step-content-edit').show();
		$('#dp-step-content-edit__backdrop').show();
		this.hadDpitRelPos = $('body').hasClass('dpit-relative-position');
		if (this.hadDpitRelPos) {
			$('body').removeClass('dpit-relative-position');
		}
		$('body').addClass('dp-noscroll');

		this.target = target;
		$('#dp-tmce-editor-tmce').trigger('click');
		this.editor = window.tinyMCE.get('dp-tmce-editor');
		this.editor.setContent(data ?? '', { format: 'html' });
		if (this.editor.selection) {
			this.editor.selection.select(this.editor.getBody(), true);
			this.editor.selection.collapse(false);
		}
		this.editor.focus();
	}


	close(wasCanceled = true) {
		this.target = null;
		this.modalOpened = false;
		$('#dp-step-content-edit').hide();
		$('#dp-step-content-edit__backdrop').hide();
		$('body').removeClass('dp-noscroll');
		if (wasCanceled && this.onTmceCancel) this.onTmceCancel();
		if (this.hadDpitRelPos) {
			$('body').addClass('dpit-relative-position');
		}
	}

}