import React, { Component } from 'react';
import { connect } from 'react-redux';
import { CustomDropdownIndicator, SelectTheme } from '../../consts/DPSelectCustomization';
import DPSelect from '../../components/DPSelect/DPSelect';
import DPButton, { ButtonWithTransition } from '../../components/DPButton/DPButton';
import TextareaAutosize from 'react-textarea-autosize';
import MultiToggle from 'react-multi-toggle';
import { StepExtDataContainerWithTransition } from '../StepExtDataContainer/StepExtDataContainer';
import classNames from 'classnames';
import * as actionTypes from '../../store/actions/action-types';
import { MultiContainerWithTransition } from '../MultiContainer/MultiContainer';
import { isTouchDevice } from '../../../../utils/dp-utils';
import TargetSelectUI from '../../components/TargetSelectUI/TargetSelectUI';
import TargetSignalIcon from '../../components/TargetSignalIcon/TargetSignalIcon';
import { ReactComponent as DesktopIcon } from '../../../../../icons/dp-desktop-solid.svg';
import { ReactComponent as MobileIcon } from '../../../../../icons/dp-mobile-alt-solid.svg';
import { ReactComponent as MobileMenuIcon } from '../../../../../icons/dp-bars-solid.svg';
import { ReactComponent as TargetIcon } from '../../../../../icons/dp-crosshairs-target.svg';
import { ReactComponent as ArrowDown } from '../../../../../icons/dp-select_arrow-down.svg';
import { ReactComponent as ArrowLeft } from '../../../../../icons/dp-arrow-left-solid.svg';
import { ReactComponent as TrashIcon } from '../../../../../icons/dp-trash-alt-regular.svg';
import { ReactComponent as SquarePlus } from '../../../../../icons/dp-square-plus-regular.svg';
import InfoIcon from '../../components/InfoIcon/InfoIcon';

const i18n = window.dpIntroTourPublicConfig.i18n;

import './dp-step-edit.scss';
import { LAYER_CLASSES } from '../../../../modules/DPIntroCore';




class StepEditor extends Component {

	constructor(props) {
		super(props);
		this.selectPosValMap = {};
		this.props.positionConfig.options.forEach(option => {
			this.selectPosValMap[option.value] = option;
		});
		this.selectHighLightValMap = {};
		this.props.highlightConfig.options.forEach(option => {
			this.selectHighLightValMap[option.value] = option;
		});

		this.alertTextsProOnly = {
			'mobileView': i18n.mobile_view_pro_only,
			'mobileMenu': i18n.mobile_menu_pro_only,
			'shortCode': i18n.short_code_pro_only
		};
	}

	onSelectTargetClick = () => {
		if (this.props.onTargetSelectionStarted) this.props.onTargetSelectionStarted();
	}

	onSelectMobileMenuTargetClick = () => {
		if (this.props.onTargetSelectionStarted) this.props.onTargetSelectionStarted('mobile_menu');
	}

	onSelectMobileTargetClick = () => {
		if (this.props.onTargetSelectionStarted) this.props.onTargetSelectionStarted('mobile');
	}

	onPositionChange = (position) => {
		this.props.setCurrentStepChange('position', position.value);
	}

	onHighlightModeChange = (highlightOption) => {
		this.props.setCurrentStepChange('highlight', highlightOption.value);
	}

	onAdvancedStepParamChanged = (id, val, advParamName = '') => {
		this.props.setCurrentStepChange(id, val, advParamName);
	}

	onIntroTextChange = (event) => {
		this.props.setCurrentStepChange('intro', event.target.value);
	}

	onSignalTargetTypeClick = (type) => {
		if (!this.props.isPRO) {
			if (confirm(this.alertTextsProOnly[type] + ' ' + i18n.would_you_like_to_check_it)) {
				window.open(this.props.proFeaturesLink, '_blank');
			}
		}
	}

	render() {
		const targetTitle = this.props.element ? i18n.change_target_el : i18n.select_target_el;

		const redirectTitle = this.props.isPRO ? i18n.redirect_to_url : i18n.multi_page_only_pro_prompt;

		let targetUIValueWideScreen = this.props.selector ? this.props.selector : i18n.no_target_selected;
		if (this.props.skip_on_big_screen === '1') targetUIValueWideScreen = i18n.step_skip_wide;

		let mobileTargetBaseVal = i18n.not_overridden_wide;
		if (!this.props.isPRO) mobileTargetBaseVal += ('\n' + i18n.skip_step_wide_mob_pro_only);

		let targetUIValueMobile = this.props.alt_mobile_selector ? this.props.alt_mobile_selector : (this.props.selector ? mobileTargetBaseVal : i18n.no_target_selected);
		if (this.props.skip_on_mobile === '1') targetUIValueMobile = i18n.step_skip_mobile;

		let targetUIValueMobileMenu = this.props.alt_mobile_selector && this.props.is_in_mobile_menu === '1' ? i18n.target_has_mob_men_alt : i18n.target_doesnt_have_mob_men_alt;
		if (!this.props.isPRO) targetUIValueMobileMenu += (' ' + i18n.mob_menu_only_pro);

		const targetMultiContainer = (
			<MultiContainerWithTransition
				classBEMRoot='dp-multi-container'
				className='dp-step-edit__multi-container'
				openDisabled={!this.props.isPRO}
				active={true}
				externalState={true}
				isOpened={this.props.isTargetDetailOpened}
				onOpenChange={this.props.updateTargetDetailOpened}
				onMenuSurroundingClick={() => { this.props.updateTargetDetailOpened(!this.props.isTargetDetailOpened); }}
			>
				<DPButton
					classBEMRoot='dp-step-edit__button'
					className={classNames({
						'dp-step-edit__button--semi-trans-bg': !this.props.element,
						'dp-step-edit__button--has-mob-selector': this.props.alt_mobile_selector,
						'dp-step-edit__button--target': true,
						'dp-pulse-infinite-light-2s': !this.props.element
					})}
					disabled={this.props.isInteractionDetailOpened}
					active={true}
					onClick={() => { if (!isTouchDevice() || !this.props.isPRO) this.onSelectTargetClick(); }}
					title={targetTitle}
				/>

				<ButtonWithTransition
					classBEMRoot='dp-button-cancel-sub'
					active={true}
					className={classNames({
						'dp-step-edit__sub-button': true,
						'dp-step-edit__mob-type-box': true,
						'dp-step-edit__mob-type-box--no-target': !this.props.selector,
						'dp-step-edit__mob-type-box--skipped': this.props.skip_on_big_screen === '1',

					})}
				>
					<TargetSelectUI
						classBEMRoot="dp-target-sel-ui"
						iconName='desktop'
						i18n={this.props.i18n}
						mainIcon={<DesktopIcon />}
						targetIcon={<TargetIcon />}
						buttonLabel={this.props.selector ? i18n.change_target : i18n.set_target}
						animateTargetIcon={true}
						skip={this.props.skip_on_big_screen}
						valueText={targetUIValueWideScreen}
						onSkipChange={val => this.props.onSkipChange('skip_on_big_screen', val)}
						targetType={i18n.wide_screen}
						targetSelector={this.props.selector}
						onSelectTargetClick={this.onSelectTargetClick}
						onClearTargetClick={this.props.onResetTargetClick}
						noTargetMsg={i18n.no_target_selected}
					/>

				</ButtonWithTransition>

				<ButtonWithTransition
					classBEMRoot='dp-button-cancel-sub'
					active={true}
					className={classNames({
						'dp-button-cancel--slower-opacity': true,
						'dp-step-edit__sub-button': true,
						'dp-step-edit__mob-type-box': true,
						'dp-step-edit__mob-type-box--no-target': !this.props.alt_mobile_selector,
						'dp-step-edit__mob-type-box--skipped': this.props.skip_on_mobile === '1',

					})}
				>
					<TargetSelectUI
						classBEMRoot="dp-target-sel-ui"
						targetType={i18n.mobile}
						iconName='mobile'
						i18n={this.props.i18n}
						mainTitle={this.props.alt_mobile_selector ? i18n.change_target_mob : i18n.override_target_mob}
						mainIcon={this.props.is_in_mobile_menu === '1' ? <MobileMenuIcon /> : <MobileIcon />}
						targetIcon={<MobileMenuIcon />}
						skip={this.props.skip_on_mobile}
						valueText={targetUIValueMobile}
						buttonLabel2={i18n.other}
						buttonLabel={i18n.in_mob_menu}
						onSkipChange={val => this.props.onSkipChange('skip_on_mobile', val)}
						targetSelector={this.props.alt_mobile_selector}
						onSelectTargetClick2={this.onSelectMobileTargetClick}
						onSelectTargetClick={this.onSelectMobileMenuTargetClick}
						onClearTargetClick={() => this.props.is_in_mobile_menu === '1'
							? this.props.onResetMobileTargetClick('mobile_menu')
							: this.props.onResetMobileTargetClick('mobile')
						}
						noTargetMsg={this.props.selector ? i18n.not_overridden_wide : i18n.no_target_selected}
					/>
				</ButtonWithTransition>

			</MultiContainerWithTransition>
		);


		const targetSignalIcons = (
			<div className={classNames({
				'dp-step-edit__signals': true,
				'dp-step-edit__signals--disabled': this.props.isInteractionDetailOpened
			})}>
				<TargetSignalIcon
					classBEMRoot="dp-step-edit__signal-icon"
					inactive={!this.props.selector}
					skip={this.props.skip_on_big_screen}
					title={targetUIValueWideScreen}
				>
					<DesktopIcon />
				</TargetSignalIcon>
				<TargetSignalIcon
					classBEMRoot="dp-step-edit__signal-icon"
					inactive={!this.props.alt_mobile_selector}
					skip={this.props.skip_on_mobile}
					title={targetUIValueMobile}
					onClick={this.props.isPRO ? this.onSelectMobileTargetClick : () => this.onSignalTargetTypeClick('mobileView')}
				>
					<MobileIcon />
				</TargetSignalIcon>
				<TargetSignalIcon
					classBEMRoot="dp-step-edit__signal-icon"
					inactive={!this.props.alt_mobile_selector || this.props.is_in_mobile_menu !== '1'}
					skip={this.props.skip_on_mobile}
					title={targetUIValueMobileMenu}
					onClick={this.props.isPRO ? this.onSelectMobileMenuTargetClick : () => this.onSignalTargetTypeClick('mobileMenu')}
				>
					<MobileMenuIcon />
				</TargetSignalIcon>
			</div>
		);



		const interactionAdvancedDisabled = this.props.isInteractionDetailOpened || this.props.isTargetDetailOpened || !this.props.element || this.props.isTargetDetailOpened;
		const interactionUI = (
			<React.Fragment>
				<MultiToggle
					className={classNames({
						'dp-step-edit__toggle': true,
						'toggleContainer--disabled': !this.props.element || this.props.isTargetDetailOpened
					})}
					options={[{
						displayName: i18n.ON,
						value: '1',
						optionClass: 'on'
					}, {
						displayName: i18n.OFF,
						value: '0',
						optionClass: 'off'
					}]}
					selectedOption={this.props.enable_interaction?.val ?? this.props.stepDefinitions?.enable_interaction?.def ?? '0'}
					onSelectOption={val => { this.onAdvancedStepParamChanged('enable_interaction', { ...this.props?.enable_interaction ?? {}, val: val }); }}
				/>
				<MultiContainerWithTransition
					classBEMRoot='dp-multi-container'
					className='dp-multi-container--slide-down'
					openDisabled={false}
					active={this.props.enable_interaction?.val == 1}
					externalState={true}
					isOpened={this.props.isInteractionDetailOpened}
					onOpenChange={(isOpened) => { if (!isOpened || !interactionAdvancedDisabled) this.props.updateInteractionDetailOpened(isOpened); }}
				>

					<ArrowDown
						className={classNames({
							'dp-step-edit__arrow-down': true,
							'dp-step-edit__arrow-down--hidden': interactionAdvancedDisabled
						})}

					/>

					<StepExtDataContainerWithTransition
						classBEMRoot='dp-step-ext-cont'
						active={true}
						val={this.props.enable_interaction}
						definition={this.props.stepDefinitions?.enable_interaction?.ext}
						onChange={(advParamName, newVal) => this.onAdvancedStepParamChanged('enable_interaction', newVal, advParamName)}
					>
						<ArrowDown
							className={classNames({
								'dp-step-edit__arrow-down': true,
								'dp-step-edit__arrow-down--up': true,
								'dp-step-edit__arrow-down--hidden': !this.props.isInteractionDetailOpened,
								'dp-step-edit__arrow-down--l': true,
								'dp-step-ext-cont__arrow-up': true

							})}
							onClick={() => this.props.updateInteractionDetailOpened(false)}
						/>
					</StepExtDataContainerWithTransition>

				</MultiContainerWithTransition>


			</React.Fragment >
		);



		return (
			<div className="dp-step-edit">
				<div className='dp-step-edit__ctrls'>
					<div className='dp-step-edit__ctrls__item' title={targetTitle}>
						<div className={classNames({
							'dp-step-edit__ctrls__item__label': true,
							'dp-step-edit__ctrls__item__label--dimmed': this.props.isInteractionDetailOpened,
						})}>{i18n.target}</div>
						{targetMultiContainer}
						{targetSignalIcons}
					</div>
					<div className='dp-step-edit__ctrls__item' title={i18n.allow_interact}>

						<div className={classNames({
							'dp-step-edit__ctrls__item__label': true,
							'dp-step-edit__ctrls__item__label--dimmed': this.props.isTargetDetailOpened,
						})}>{i18n.interaction}</div>
						{interactionUI}
					</div>
					<div className='dp-step-edit__ctrls__item' title={!this.props.element ? i18n.set_target_first : i18n.tooltip_pref_pos}>

						<div className={classNames({
							'dp-step-edit__ctrls__item__label': true,
							'dp-step-edit__ctrls__item__label--dimmed': this.props.isTargetDetailOpened || this.props.isInteractionDetailOpened,
						})}>{i18n.position}</div>
						<DPSelect
							className={classNames({
								'dp-select': true,
								'dp-step-edit__select': true,
								'dp-select--disabled': !this.props.element || this.props.isTargetDetailOpened || this.props.isInteractionDetailOpened
							})}
							classNamePrefix='dp-select'
							options={this.props.positionConfig.options}
							value={this.selectPosValMap[this.props.position || '']}
							escapeClearsValue
							hideSelectedOptions
							placeholder={this.props.positionConfig.options[this.props.positionConfig.defIdx].label}
							theme={SelectTheme}
							components={{ DropdownIndicator: CustomDropdownIndicator }}
							onChange={this.onPositionChange}
						/>
					</div>

					<div className='dp-step-edit__ctrls__item' title={redirectTitle}>

						<div className={classNames({
							'dp-step-edit__ctrls__item__label': true,
							'dp-step-edit__ctrls__item__label--dimmed': this.props.isTargetDetailOpened || this.props.isInteractionDetailOpened,
						})}>{i18n.URL}</div>
						<DPButton
							classBEMRoot='dp-step-edit__button'
							className={classNames({
								'dp-step-edit__button--url': true
							})}
							disabled={!this.props.isPRO || this.props.isTargetDetailOpened || this.props.isInteractionDetailOpened}
							active={true}
							onClick={this.props.onUrlRedirectClick}
							title={redirectTitle}
						/>
					</div>
				</div>
				<div className='dp-step-edit__wrap'>
					<div className='dp-step-edit__ta-wrap'>
						<label className='dp-step-edit__label'>
							{(this.props.intro ? i18n.edit_tour_tip_text : i18n.enter_tour_tip_text)}
							<span onClick={this.props.isPRO ? null : () => this.onSignalTargetTypeClick('shortCode')} className={classNames({ 'dpit-pro-only': !this.props.isPRO })}>{' / ' + i18n.short_code}</span>
							<InfoIcon
								classBEMRoot="dp-step-edit__label__icon"
								className="dp-info-icon"
								infoText={this.props.isPRO
									? i18n.new_short_code_needs_reload
									: this.alertTextsProOnly['shortCode']}
							/>
						</label>

						<TextareaAutosize
							className={classNames({
								'dp-step-edit__intro-text': true,
								'dp-step-edit__intro-text--higher-on-mobile': this.props.isTargetDetailOpened || this.props.isInteractionDetailOpened
							})}
							value={this.props.intro || ''}
							placeholder={'Enter tour tip text'}
							minRows={4}
							maxRows={10}
							onClick={this.props.onContentEditClick}
						//onChange={this.onIntroTextChange}
						/>
					</div>
					<div className='dp-step-edit__ctrls-2'>
						<div className='dp-step-edit__ctrls-2__item' title={!this.props.element ? i18n.set_target_first : i18n.tooltip_pref_highlight}>
							<div className={classNames({
								'dp-step-edit__label': true,
								'dp-step-edit__ctrls__item__label--dimmed': this.props.isTargetDetailOpened || this.props.isInteractionDetailOpened,
							})}>{i18n.highlight}</div>
							<DPSelect
								className={classNames({
									'dp-select': true,
									'dp-step-edit__select': true,
									'dp-step-edit__select--highlight': true,
									'dp-select--disabled': !this.props.element || this.props.isTargetDetailOpened || this.props.isInteractionDetailOpened
								})}
								classNamePrefix='dp-select'
								options={this.props.highlightConfig.options}
								value={this.selectHighLightValMap[this.props.highlight || '']}
								escapeClearsValue
								hideSelectedOptions
								placeholder={this.props.highlightConfig.options[this.props.highlightConfig.defIdx].label}
								theme={SelectTheme}
								components={{ DropdownIndicator: CustomDropdownIndicator }}
								onChange={this.onHighlightModeChange}
							/>
						</div>
					</div>
				</div>
				<div className='dp-step-edit__navig'>
					<DPButton
						classBEMRoot='dp-step-edit__navig__button'
						className='dp-step-edit__navig__button--back'
						//text={this.props.navigLabeling.prevLabel || i18n.back}
						title={i18n.go_to_prev_step}
						active={true}
						onClick={this.props.onPreviousStepButtonClick}
						disabled={this.props.step <= 1}
						hovered={this.props.isNavigButtonBackHovered}
						onMouseEnter={() => { $(`.${LAYER_CLASSES.buttonPrev}`).addClass('hovered'); }}
						onMouseLeave={() => { $(`.${LAYER_CLASSES.buttonPrev}`).removeClass('hovered'); }}
					>
						<ArrowLeft
							className={classNames({
								'dp-step-edit__navig__btn-ico': true,
								'dp-step-edit__navig__btn-ico--left': true
							})}
						/>
					</DPButton>

					<DPButton
						classBEMRoot='dp-step-edit__navig__button'
						className='dp-step-edit__navig__button--next'
						title={i18n.go_to_next_step}
						active={true}
						onClick={this.props.onNextStepButtonClick}
						disabled={this.props.step >= this.props.stepCount}
						hovered={this.props.isNavigButtonNextHovered}
						onMouseEnter={() => { $(`.${LAYER_CLASSES.buttonNext}`).addClass('hovered'); }}
						onMouseLeave={() => { $(`.${LAYER_CLASSES.buttonNext}`).removeClass('hovered'); }}
					>
						<ArrowLeft
							className={classNames({
								'dp-step-edit__navig__btn-ico': true,
								'dp-step-edit__navig__btn-ico--right': true
							})}
						/>
					</DPButton>

					<DPButton
						classBEMRoot='dp-step-edit__navig__button'
						className='dp-step-edit__navig__button--add'
						title={i18n.add_after_hint}
						active={true}
						onClick={this.props.addNewStepAfterFce}
					>
						<SquarePlus
							className={classNames({
								'dp-step-edit__navig__btn-ico': true,
								'dp-step-edit__navig__btn-ico--remove': true
							})}
						/>
					</DPButton>

					<DPButton
						classBEMRoot='dp-step-edit__navig__button'
						className='dp-step-edit__navig__button--remove'
						//text={i18n.remove}
						title={i18n.remove_current_step}
						active={true}
						onClick={this.props.onRemoveButtonClick}
					>
						<TrashIcon
							className={classNames({
								'dp-step-edit__navig__btn-ico': true,
							})}
						/>
					</DPButton>


				</div>
			</div >
		);

	}
}
const mapStateToProps = (state) => ({
	intro: state.introStep.intro,
	position: state.introStep.position,
	highlight: state.introStep.highlight,
	url: state.introStep.url,
	enable_interaction: state.introStep.enable_interaction,
	element: state.introStep.element,
	selector: state.introStep.selector,
	skip_on_big_screen: state.introStep.skip_on_big_screen,
	skip_on_mobile: state.introStep.skip_on_mobile,
	is_in_mobile_menu: state.introStep.is_in_mobile_menu,
	alt_mobile_selector: state.introStep.alt_mobile_selector,
	step: state.introStep.step,
	stepCount: state.app.stepCount,
	isNewStep: state.app.isNewStep,
	isPRO: state.app.isPRO,
	isNavigButtonNextHovered: state.app.isNavigButtonNextHovered,
	isNavigButtonBackHovered: state.app.isNavigButtonBackHovered,
	isTargetDetailOpened: state.app.isTargetDetailOpened,
	isInteractionDetailOpened: state.app.isInteractionDetailOpened
});

const mapDispatchToProps = dispatch => {
	return {
		updateTargetDetailOpened: (isOpened) => dispatch({ type: actionTypes.UPDATE_TARGET_EDIT_DETAIL_OPENED, isOpened: isOpened }),
		updateInteractionDetailOpened: (isOpened) => dispatch({ type: actionTypes.UPDATE_INTERACTION_EDIT_DETAIL_OPENED, isOpened: isOpened })
	};
};


export default connect(mapStateToProps, mapDispatchToProps)(StepEditor);
