import { Component } from 'react';
import classNames from 'classnames';
import unique from 'unique-selector';
import { $S } from '../../../../utils/dp-utils';
import DPBoundingBox from './../../../../modules/DPBoundingBox';


import './dp-target-selector.scss';

export default class TargetSelector extends Component {
	jVisualizer = null;

	componentDidMount() {
		if (this.props.dpDebugEn) console.log('TargetSelector');
		this.updateBlackLists();
		//console.log('target selector');
	}

	componentDidUpdate(prevProps) {

		this.updateBlackLists();
		if (!prevProps.active && this.props.active) {
			this._enterSelectElementMode();
		}
		else if (prevProps.active && !this.props.active) {
			this._exitSelectElementMode();
		}
		if (this.props.hideConfirmed && this.jVisualizer) {
			this.jVisualizer.fadeOut(500);
		}
	}

	updateBlackLists() {
		this.blackList = this.getClassArray(this.props.selectorBlackList);
		if (this.props.classBEMRoot) this.blackList.push('.' + this.props.classBEMRoot);
		this.blackListExit = this.getClassArray(this.props.selectorBlackListExit);
		this.blacklistTotal = [...this.blackList, ...this.blackListExit];
	}

	getClassArray(classes) {
		let res = [];
		if (Array.isArray(classes)) {
			res = [...classes];
		}
		else if (typeof (classes) === 'string') {
			res = classes.split(' ');
		}
		return res;
	}

	_enterSelectElementMode = () => {
		const mainClasses = classNames({
			[this.props.classBEMRoot]: true,
			[this.props.className]: this.props.className,
			[this.props.classBEMRoot + '--active']: this.props.active
		});

		this.jVisualizer = ($('.' + this.props.classBEMRoot));
		if (!this.jVisualizer.length) {
			this.jVisualizer = $(`<div class="${mainClasses}"></div>`).prependTo(this.props.container);
		}
		else {
			this.jVisualizer.attr('class', '');
			this.jVisualizer.addClass(mainClasses);
		}
		this.jVisualizer.fadeIn(700, () => {
			$(document).on(this.props.clickEventName, this._selectElement);
			if (this.props.clickEventName != 'click') $(document).on('click', this._stopEventPropagation);
			$(document).on('mousemove', '*', this._visualizeHoveredElement);
			$(document).on('mouseenter', '*', this._onMouseEnterVisualizeEl);
			$(document).on('mouseleave', '*', this._onMouseLeaveVisualizeEl);
		});

		// Initial position
		if (this.props.initialTargetElement) this._adjustVisualizer($S(this.props.initialTargetElement), this.props.suppressTargetClickEvents);
	}

	_exitSelectElementMode = (hideVisualizer = true) => {
		$(document).off('mousedown', this._selectElement);
		$(document).off('click', this._selectElement);
		//if (this.props.clickEventName != 'click') $(document).off('click', this._stopEventPropagation);
		$(document).off('mousemove', '*', this._visualizeHoveredElement);
		$(document).off('mouseenter', '*', this._onMouseEnterVisualizeEl);
		$(document).off('mouseleave', '*', this._onMouseLeaveVisualizeEl);
		$('.' + this.props.visualizedTargetClass).removeClass(this.props.visualizedTargetClass);

		if (hideVisualizer && this.jVisualizer && this.jVisualizer.length && !this.props.leaveConfirmedVisible) {
			this.jVisualizer.fadeOut(500);
		}
	}

	_stopEventPropagation = (event) => {
		event.preventDefault();
		event.stopPropagation();
		return false;
	}

	_onMouseEnterVisualizeEl = (event) => {
		if (!this._isTargetOnBlackList($(event.target), this.blacklistTotal)) {
			$(event.target).addClass(this.props.visualizedTargetClass);
		}
	}

	_onMouseLeaveVisualizeEl = (event) => {
		$(event.target).removeClass(this.props.visualizedTargetClass);
	}


	_visualizeHoveredElement = (event) => {
		this._adjustVisualizer($(event.target), this.props.suppressTargetClickEvents);
	}

	_isTargetOnBlackList(jTarget, blackList) {
		for (const val of blackList) {
			if (jTarget.closest(val).length) return true;
		}
		return false;
	}

	_adjustVisualizer = (jTarget, unbindClick = true) => {
		if (jTarget) {
			if (!this._isTargetOnBlackList(jTarget, this.blacklistTotal)) {
				this.jVisualizer.show();
				if (unbindClick) jTarget.unbind('click');
				this.jVisualizer.offset(jTarget.offset());
				this.jVisualizer.outerWidth(jTarget.outerWidth());
				this.jVisualizer.outerHeight(jTarget.outerHeight());
			}
			else {
				this.jVisualizer.hide();
			}
		}
	}

	_isElementSamePosAndSize = (jel1, jel2, tolerancePx) => {
		if (jel1 && jel1.length && jel2 && jel2.length) {
			if (document.documentElement === jel1[0]) return false;
			const bb1 = DPBoundingBox.getElBoundingBox(jel1[0], true, false);
			const bb2 = DPBoundingBox.getElBoundingBox(jel2[0], true, false);
			if (bb1 && bb2) {
				const keys = Object.keys(bb1);
				for (const key of keys) {
					if (key != 'width' && key != 'height') {
						if (Math.abs(bb1[key] - bb2[key]) > tolerancePx) return false;
					}
				}
				return true;
			}
		}
		return false;
	}


	_trySelectParent = (jElement) => {
		let res = jElement;
		while (this._isElementSamePosAndSize(jElement, res.parent(), 3)) {
			res = res.parent();
		}
		return res;
	}



	_selectElement = (event) => {
		if (event.which === 1) {
			const isOnExitBL = this._isTargetOnBlackList($(event.target), this.blackListExit);
			const isOnBL = this._isTargetOnBlackList($(event.target), this.blackList);

			if ((isOnExitBL && !isOnBL) || (!isOnExitBL && !isOnBL)) {
				this._exitSelectElementMode(false);
			}

			if (isOnExitBL && !isOnBL /* sub button to switch exit btn position */) {

				if (this.props.initialTargetElement) this._adjustVisualizer($S(this.props.initialTargetElement), this.props.suppressTargetClickEvents);
				//this.jVisualizer.removeClass(this.props.classBEMRoot + '--active');
			}

			if ((!isOnExitBL && !isOnBL)) {
				event.preventDefault();
				event.stopPropagation();
				let jSelectedEl = null;
				switch (this.props.mode) {
					case 'MOST_PARENT': jSelectedEl = this._trySelectParent($(event.target)); break;
					default: jSelectedEl = $(event.target);
				}
				let selector = '';
				let isError = false;
				try {
					selector = unique(jSelectedEl[0], {
						selectorTypes: this.props.selectorTypes
					});

					this.jVisualizer.addClass(this.props.classBEMRoot + '--confirmed');

				}
				catch (exc) {
					if (jSelectedEl[0] && jSelectedEl[0].id) {
						selector = `#${jSelectedEl[0].id}`;
					}
					else {
						isError = true;
					}
				}
				setTimeout(() => {

					this.props.onTargetSelected(selector, jSelectedEl[0], isError);
				}, 1000); //wait for final animation
			}

			return false;
		}
	}

	render = () => null;
}