import React, { Component } from 'react';
import classNames from 'classnames';
import MultiToggle from 'react-multi-toggle';
import DPButton from '../DPButton/DPButton';
import { ReactComponent as TrashIcon } from '../../../../../icons/dp-trash-alt-regular.svg';
import { sprintf } from 'sprintf-js';

import './dp-target-sel-ui.scss';


export default class TargetSelectUI extends Component {
	render() {
		const mainClasses = classNames({
			[this.props.classBEMRoot]: true,
			[this.props.className]: this.props.className,
		});
		const valueClasses = classNames({
			[`${this.props.classBEMRoot}__main__value`]: true,
			[`${this.props.classBEMRoot}__main__value--no-target`]: !this.props.targetSelector,
			[`${this.props.classBEMRoot}__main__value--skipped`]: this.props.skip === '1',
		});

		const mainRowClasses = classNames({
			[`${this.props.classBEMRoot}__main__row`]: true,
			[`${this.props.classBEMRoot}__main__row--center`]: !this.props.targetSelector,
		});

		const buttonClasses = classNames({
			[`${this.props.classBEMRoot}__main__button--half`]: this.props.onSelectTargetClick2,
		});

		const mainIconClasses = classNames({
			[`${this.props.classBEMRoot}__control__icon`]: true,
			[`${this.props.classBEMRoot}__control__item`]: true,
			'dp-step-edit__mob-type-icon': true,
			'dp-step-edit__mob-type-icon--no-target': !this.props.targetSelector,
			'dp-step-edit__mob-type-icon--skipped': this.props.skip === '1',
		});

		const targetButtonIconClass = classNames({
			[`${this.props.classBEMRoot}__main__button__icon`]: true,
			'dp-pulse-infinite-light-2s': !this.props.targetSelector && this.props.skip !== '1' && this.props.animateTargetIcon,
		});

		return (
			<div
				className={mainClasses}
				onClick={this.props.onClick ? this.props.onClick : (event) => { event.stopPropagation(); return false; }}
				title=""
			>
				{' '}
				<div className={`${this.props.classBEMRoot}__control`}>
					<div className={mainIconClasses}>{this.props.mainIcon}</div>
					{this.props.onSkipChange
						? (
							<div title={sprintf(this.props.i18n.skip_or_show, this.props.targetType || '')}>
								<MultiToggle
									className={`${this.props.classBEMRoot}__control__skip ${this.props.classBEMRoot}__control__item`}
									options={[{
										displayName: this.props.i18n.SHOW,
										value: '0',
										optionClass: 'on',
									}, {
										displayName: this.props.i18n.SKIP,
										value: '1',
										optionClass: 'off',
									}]}
									selectedOption={this.props.skip ? this.props.skip : '0'}
									onSelectOption={this.props.onSkipChange}
								/>
							</div>
						) : null}
					<div className={`${this.props.classBEMRoot}__control__label ${this.props.classBEMRoot}__control__item`}>{this.props.targetType ? ` ${this.props.targetType}` : ''}</div>
				</div>

				<div className={`${this.props.classBEMRoot}__main`}>
					{this.props.mainTitle ? (
						<div className={classNames({
							[`${this.props.classBEMRoot}__main__title`]: true,
							[`${this.props.classBEMRoot}__main__title--active`]: this.props.skip !== '1',
						})}
						>{this.props.mainTitle}
						</div>
					) : null}
					<div className={mainRowClasses}>
						<DPButton
							classBEMRoot={`${this.props.classBEMRoot}__main__button`}
							className={buttonClasses}
							active={this.props.skip !== '1'}
							title={this.props.buttonTitle ? this.props.buttonTitle : this.props.buttonLabel}
							onClick={this.props.onSelectTargetClick}
						>
							{this.props.targetIcon ? <div className={targetButtonIconClass}>{this.props.targetIcon}</div> : null}
							<div className={`${this.props.classBEMRoot}__main__button__label`}>
								{' '}
								{this.props.buttonLabel}
								{' '}
							</div>
						</DPButton>
						{this.props.onSelectTargetClick2
							? (
								<DPButton
									classBEMRoot={`${this.props.classBEMRoot}__main__button`}
									className={buttonClasses}
									active={this.props.skip !== '1'}
									title={this.props.buttonTitle2 ? this.props.buttonTitle2 : this.props.buttonLabel2}
									onClick={this.props.onSelectTargetClick2}
								>
									{this.props.targetIcon2 ? <div className={targetButtonIconClass}>{this.props.targetIcon2}</div> : null}
									<div className={`${this.props.classBEMRoot}__main__button__label`}>{this.props.buttonLabel2}</div>
								</DPButton>
							)
							: null}
					</div>
					<div className={`${this.props.classBEMRoot}__main__row`}>
						<div className={valueClasses} title={this.props.valueText}>{this.props.valueText}</div>
						<DPButton
							classBEMRoot={`${this.props.classBEMRoot}__main__trash`}
							active={this.props.targetSelector && this.props.skip !== '1'}
							title={this.props.i18n.clear_target}
							onClick={this.props.onClearTargetClick}
						>
							<TrashIcon className={`${this.props.classBEMRoot}__control__trash__icon`} />
						</DPButton>

					</div>
				</div>
			</div>
		);
	}
}
