import React from 'react';
import { components } from 'react-select';
import { LAYER_CLASSES } from '../../../modules/DPIntroCore';



export const CustomDropdownIndicator = (
	props,
) => (
	<components.DropdownIndicator {...props}>
		{
			props.selectProps.menuIsOpen
				? <div className="dp-indicator dp-indicator--opened" />
				: <div className="dp-indicator dp-indicator--closed" />
		}
	</components.DropdownIndicator>
);

export const SelectTheme = (theme) => {
	let bAdminColor = null;
	let introWrapper = $('.' + LAYER_CLASSES.wrapper);
	if (introWrapper.length) {
		bAdminColor = getComputedStyle(introWrapper[0]).getPropertyValue('--dpu-color-3');
	}
	if (!bAdminColor) bAdminColor = '34, 113, 177';
	return {
		...theme,
		borderRadius: 2,
		colors: {
			...theme.colors,
			primary25: `rgba(${bAdminColor},0.25)`,
			primary50: `rgba(${bAdminColor},0.55)`,
			primary75: `rgba(${bAdminColor},0.75)`,
			primary: `rgb(${bAdminColor})`,
		},
		spacing: {
			...theme.spacing,
			baseUnit: 4,
			controlHeight: 30,
			menuGutter: 16,
		},
	};
};

