import React, { Component, Fragment } from 'react';
//import { Link } from 'react-router-dom';
import classNames from 'classnames';
import WithTransition from './../../hoc/WithTransition';


class DPButton extends Component {

	lastText = '';

	render() {

		const mainClasses = classNames({
			[this.props.classBEMRoot]: true,
			[this.props.className]: this.props.className,
			[this.props.classBEMRoot + '--active']: this.props.active,
			[this.props.classBEMRoot + '--disabled']: this.props.disabled,
			'hovered': this.props.hovered
		});

		let text = (this.props.inTransition) ? this.lastText : this.props.text;
		this.lastText = text;

		const title = this.props.title === undefined ? this.props.text : this.props.title;

		const buttonContent = (
			<Fragment>
				{this.props.children}
				{text ? (!this.props.addSpanForText ? text : <span className={this.props.classBEMRoot + '__text'}>{text}</span>) : null}
			</Fragment>
		);

		let button = null;

		/*if (this.props.linkTo) {
			button = (
				<Link className={mainClasses} onMouseEnter={this.props.onMouseEnter} onMouseLeave={this.props.onMouseLeave} to={this.props.linkTo} title={title} style={this.props.buttonStyle} onClick={this.props.onClick}>
					{buttonContent}
				</Link>
			);
		}*/
		if (this.props.href) {
			button = (
				<a className={mainClasses} onMouseEnter={this.props.onMouseEnter} onMouseLeave={this.props.onMouseLeave} href={this.props.href} title={title} target={this.props.target} /* rel="noopener noreferrer" */ style={this.props.buttonStyle} onClick={this.props.onClick}>
					{buttonContent}
				</a>
			);
		}
		else {
			button = (
				<div onMouseEnter={this.props.onMouseEnter} onMouseLeave={this.props.onMouseLeave} disabled={this.props.disabled} className={mainClasses} href={this.props.href} title={title} style={this.props.buttonStyle} onClick={this.props.onClick}>
					{buttonContent}
				</div>
			);
		}
		return button;
	}
}
export default DPButton;
export const ButtonWithTransition = WithTransition(DPButton);

